.popup__container {
  position: relative;
  display: flex;
  flex-direction: column;
  padding: 35px 36px;
  background-color: white;
  color: black;
  width: 49%;
  min-width: 282px;
  max-width: 430px;
  min-height: 330px;
  border-radius: 10px;
  border: none;
  box-sizing: border-box;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
}

@media screen and (max-width: 619px) {
  .popup__container {
    width: 90%;
    max-width: 282px;
    padding: 26px 22px;
    min-height: 322px;
  }
}
